import React from 'react';
import { Card, Form } from 'antd';
import { NodePanel, EdgePanel, GroupPanel, MultiPanel, DetailPanel, CanvasPanel } from 'gg-editor';
import DetailForm from './DetailForm';
import './index.less';
import { connect } from 'react-redux';
import PubSub from 'pubsub-js';
import NodeItemPanel from './NodeItemPanel';
import { saveItemObj } from '../store/actionCreator';
// import ModelPanel from './configurationCards/modelPanel';
// import {  LocalDataCard, KNN, SVMNline, DensityCluster } from './configurationCards/index';

class FlowDetailPanel extends React.Component {

  showNodeDetail = () => {
    const { currentNode, configCurrentKey, label } = this.props;
    this.token = PubSub.subscribe('nodeItem',(event, data) => {
      console.log(data);
    })
    return (
      this.renderConfiguration(configCurrentKey, label, currentNode)
    )
  }
  
  renderConfiguration = (current, label, currentNode) => {
      this.token = PubSub.subscribe('nodeItem',(event, data) => {
        window.ITEM = data
      })
      if(currentNode) {
        let detail = currentNode;
        if(current === 'configuration'){
          console.log(detail.label);
          switch(detail.componentId){
            // case 29:
            //   return (<ModelPanel detail={detail}/>);
            // case 2:
            //   return (<LocalDataCard detail={detail}/>);
            // case 11:
            //   return (<KNN detail={detail} />);
            // case 41:
            //   return (<SVMNline detail={detail} />)
            // case 38:
            //   return (<DensityCluster detail={detail} />)
            default:
              return <NodeItemPanel detail={detail}/>
          }
        } else {
          return (
            <Card title='模块使用及参数说明' bordered={true} bodyStyle={{background: '#fff'}}>
              <Form></Form>
            </Card>
          )
        }
      }else{
        return (
          <Card title='组件参数配置' bordered={true} bodyStyle={{background: '#fff',height:400}}>
            <Form></Form>
          </Card>
        )
      }
  }
  componentWillUnmount(){
    this.props.saveItem(undefined);
  }

// const FlowDetailPanel = () => {
  
  render() {
    return (
      <DetailPanel className="detailPanel">
        <NodePanel>
          <DetailForm type="node" />
        </NodePanel>
        <EdgePanel>
          <Card type="inner" size="small" bordered={false} bodyStyle={{background: '#fff'}}>
            {this.showNodeDetail()}
          </Card>
        </EdgePanel>
        {/* <GroupPanel>
          <DetailForm type="group" />
        </GroupPanel> */}
        <MultiPanel>
          <Card type="inner" size="small" title="Multi Select" bordered={false} />
        </MultiPanel>
        <CanvasPanel>
          <Card type="inner" size="small" bordered={false} bodyStyle={{background: '#fff'}}>
            {this.showNodeDetail()}
          </Card>
        </CanvasPanel>
      </DetailPanel>
    );
  }
};

const mapStateToProps = (state) => {
  return {
    configCurrentKey: state.editor.configCurrentKey,
    label: state.editor.label,
    currentNode: state.editor.currentNode,
  }
}
const mapDispatchToProps=(dispatch)=>({
  saveItem(obj){
    dispatch(saveItemObj(obj));
  }
})

export default connect(mapStateToProps, mapDispatchToProps)(FlowDetailPanel);
// export default FlowDetailPanel;

