import React from 'react';
import { Row, Col, Drawer } from 'antd';
import GGEditor, { Flow } from 'gg-editor';
import EditorMinimap from '../components/EditorMinimap';
import { FlowContextMenu } from '../components/EditorContextMenu';
import { FlowToolbar } from '../components/EditorToolbar';
import { FlowItemPanel } from '../components/EditorItemPanel';
import { FlowDetailPanel } from '../components/EditorDetailPanel';
import CustomNode from '../components/node/nodeTensorflow';
import StartNode from '../components/node/nodeStart';
import styles from './index.module.less';

import IconFont from '../../common/IconFont';
import { NodePanel, DetailPanel } from 'gg-editor';
import DetailForm from '../components/EditorDetailPanel/DetailForm';
import '../components/EditorDetailPanel/index.less';

class Home extends React.Component {

  constructor(props){
    super(props);
    
    this.state = {
      
    }
  }

  // componentWillUnmount(){
  // }

  lockIconVis = () => {
    const { unlockDrawer, lockDrawer, lock, rightSideVis } = this.props;
    return (
      <div className={styles.lockIcon}>
        {/* <IconFont type="icon-suo" className={ lock ? styles.lock : styles.unlock } onClick={ lock ? unlockDrawer: lockDrawer}/> */}
        <IconFont type="icon-suo" className={styles.lock} />
      </div>
    )
  }

  componentDidMount(){
    this.setState({processObj:''})
    // this.props.resetProcess('');
  }

  render() {
    
    const { handleNodeClick, handleCanvasClick, rightSideVis, lock, currentNode } = this.props;

    return (
      <GGEditor className={styles.editor}>
        <Row type="flex" className={styles.editorBd} style={{height:'100%'}}>
          <Col xxl={4} xl={5} lg={6} md={7} sm={8} xs={8} className={styles.editorSidebar}>
            <FlowItemPanel />
          </Col>
          <Col xxl={20} xl={19} lg={18} md={17} sm={16} xs={16} className={styles.editorContent}>
            <FlowToolbar />
            <CustomNode />
            <StartNode />
            <Flow 
              className={styles.flow}
              // onNodeClick={(e)=>{handleNodeClick(e);}}
              // onClick={(e)=>{handleCanvasClick(rightSideVis, lock, currentNode);}}
              data={this.state.processObj}
              />
          </Col>
        </Row>
        <FlowContextMenu />
        <Drawer
            placement="right"
            mask={false}
            style={{marginTop:'64px'}}
            width='400px'
            closable={false}
            visible={ rightSideVis || lock }
          >
            {this.lockIconVis()}
            <FlowDetailPanel/>
            <EditorMinimap/> 
          </Drawer>
      </GGEditor>
    );
  }
};



export default Home;
