import React, { Component, Fragment } from 'react';
import { withPropsAPI, Command, Toolbar } from "gg-editor";
// import { connect } from 'react-redux';
import { actionCreators } from '../../store';
import styles from '../../index.module.less';
import './index.less';
import { Modal, Input, message, Tooltip, Button, Icon } from 'antd';


class Newprocess extends Component {

  constructor(props){
    super(props);
    this.state ={
      visible: false,
      nameValue:''
    }
  }

  handleOk = e => {
    console.log(e);
    const { nameValue } = this.state
    const { saveProcessName, saveProcessId, saveProject } = this.props
    if(nameValue){
      saveProcessName(nameValue);
      saveProject('','');
      saveProcessId('');
      this.setState({
        visible: false,
        nameValue: ''
      });
    }else{
      message.info('请输入流程名称！')
    }
  };

  handleCancel = e => {
    console.log(e);
    this.setState({
      visible: false,
      nameValue: ''
    });
  };

  handleChange = e => {
    this.setState({
      nameValue: e.target.value,
    });
  }

  showModal = () => {
    const { saveProcessName, saveProcessId, saveProject } = this.props
    saveProcessName('');
    saveProject('','');
    saveProcessId('');
    this.setState({
      visible: true,

    });
  };

  render(){
    const text = <span>tips:&nbsp;&nbsp;新建流程会清空画布</span>;
    return(
      <Fragment>
        {/* <Toolbar className="clearCommand">
          <Command name="clear">
            <Tooltip placement="bottomLeft" title={text}>
              <span className='operatorSpan' onClick={this.showModal}>新建</span>
            </Tooltip>
          </Command>
        </Toolbar> */}

        <Command name="clear">
          <div className={styles.com} title="新建" onClick={this.showModal}>
            <Tooltip placement="bottomLeft" title={text}>
              <Icon type="plus" /><span>新建</span>
            </Tooltip>
          </div>
        </Command>
        <Modal
          title="流程"
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          footer={[
            <Button onClick={this.handleOk} type='primary' >
              确定
            </Button>,
            <Button onClick={this.handleCancel}>
              取消
            </Button>
          ]}
        >
          流程名称：<Input placeholder="请输入流程名称" onChange={this.handleChange} value={this.state.nameValue} style={{marginTop:'8px'}}/>
        </Modal>
      </Fragment>
    )
  }
}



const mapDispatchToProps = dispatch => {
  return{
    saveProcessId(processId){
      dispatch(actionCreators.saveProcessId(processId))
    },
    saveProcessName(nameValue){
      dispatch(actionCreators.saveProcessName(nameValue))
    },
    saveProject(obj, xmlString){
      dispatch(actionCreators.saveProject(obj, xmlString))
    }
  }
}

// export default connect(null, mapDispatchToProps)(withPropsAPI(Newprocess));
export default withPropsAPI(Newprocess);

