import React, { Fragment } from 'react';
import { Card, Form, Input, Select, Menu } from 'antd';
import { withPropsAPI } from 'gg-editor';
import { connect } from 'react-redux';
import { actionCreator } from '../store';
import PubSub from 'pubsub-js';
import NodeItemPanel from './NodeItemPanel';
// import { LocalDataCard, KNN, SVMNline, DensityCluster } from './configurationCards/index';
// import ModelPanel from './configurationCards/modelPanel';

const { Item } = Form;
const { Option } = Select;

const inlineFormItemLayout = {
  labelCol: {
    sm: { span: 8 },
  },
  wrapperCol: {
    sm: { span: 16 },
  },
};

class DetailForm extends React.Component {

  get item() {
    const { propsAPI } = this.props;
    console.log(propsAPI.getSelected()[0]);
    return propsAPI.getSelected()[0];
  }

  handleSubmit = (e) => {
    if (e && e.preventDefault) {
      e.preventDefault();
    }

    const { form, propsAPI } = this.props;
    const { getSelected, executeCommand, update } = propsAPI;

    setTimeout(() => {
      form.validateFieldsAndScroll((err, values) => {
        if (err) {
          return;
        }

        const item = getSelected()[0];

        if (!item) {
          return;
        }

        executeCommand(() => {
          update(item, {
            ...values,
          });
        });
      });
    }, 0);
  };


  renderConfiguration = (current, label) => {
    if(current === 'configuration'){
      let nodeItem = this.item;
      PubSub.publish('nodeItem',nodeItem);
      let detail = this.item.getModel();
      this.props.saveItem(detail);
      switch(detail.componentId){
        // case 29:
        //   return (<ModelPanel detail={detail}/>);
        // case 2:
        //   return (<LocalDataCard detail={detail}/>);
        // case 11:
        //   return (<KNN detail={detail} />);
        // case 41:
        //   return (<SVMNline detail={detail} />);
        // case 38:
        //   return (<DensityCluster detail={detail} />)
        default:
          return <NodeItemPanel detail={detail}/>
      }
    } else {
      return (
        <Card title='模块使用及参数说明' bordered={true} bodyStyle={{background: '#fff'}}>
          <Form></Form>
        </Card>
      )
    }
  }

  render() {
    if (!this.item) {
      console.log('tail')
      return (
        <Card title='组件参数配置' bordered={true} bodyStyle={{background: '#fff'}}>
        </Card>
      );
    }

    return (
      <Card type="inner" size="small" bordered={false} bodyStyle={{background: '#fff'}}>
        {/* <Form onSubmit={this.handleSubmit}>
          {type === 'node' && this.renderNodeDetail()}
          {type === 'edge' && this.renderEdgeDetail()}
          {type === 'group' && this.renderGroupDetail()}
        </Form> */}
        {/* <Menu onClick={this.props.handleClick} selectedKeys={[this.props.configCurrentKey]} mode="horizontal">
          <Menu.Item key="configuration">
            参数配置
          </Menu.Item>
          <Menu.Item key="description">
            描述说明
          </Menu.Item>
        </Menu> */}
        {this.renderConfiguration(this.props.configCurrentKey, this.props.label)}
      </Card>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    configCurrentKey: state.editor.configCurrentKey,
    label: state.editor.label,
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    saveItem(item) {
      console.log(item)
      dispatch(actionCreator.saveItemObj(item));
    }
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Form.create()(withPropsAPI(DetailForm)));
// export default Form.create()(withPropsAPI(DetailForm));
