import * as constants from './constants';

const defaultState = {
    rerunningVisible : false,
    runningInstanceVisible : false,
    historyVisible : false,
    processId: '',
    xmlString: '',
    GGEditorObj:'',
    processState: 0
};

export default (state=defaultState, action) =>  {
    const newState = JSON.parse(JSON.stringify(state));
    switch(action.type) {
      case constants.CHANGE_RERUNNING_VISIBLE:
        newState.rerunningVisible = !newState.rerunningVisible;
        return newState;
      case constants.CHANGE_RUNNING_INSTANCE_VISIBLE:
        newState.runningInstanceVisible = !newState.runningInstanceVisible;
        return newState;
      case constants.CHANGE_HISTORY_VISIBLE:
        newState.historyVisible = !newState.historyVisible;
        return newState;
      case constants.SAVE_PROJECT :
        const { obj, xmlString } = action;
        return { ...newState, GGEditorObj:obj, xmlString };
      case constants.SAVE_PROCESSID :
          const { processId } = action;
          return { ...newState, processId };
      case constants.SAVE_PROCESSNAME :
          const { processName } = action;
          return { ...newState, processName };
      case constants.SAVE_NODESID :
        const { nodesId } = action;
        return { ...newState, nodesId };
      case constants.SAVE_RESULT_LIST :
        const { resultListData, isModel, processLogId } = action;
        return { ...newState, resultList:resultListData, isModel, processLogId };
      case constants.SAVE_PROCESS_STATE :
        const { processState } = action;
        return { ...newState, processState };
      default:
      return state;
    }
}