import React, { Component } from 'react';
import { Link, withRouter } from 'react-router-dom'
import { Table, Divider, Spin, message, PageHeader, Modal, Row, Col, Input,Button } from 'antd';
import { getModelList, deleteModel, getSearchData, } from '../../api/model.js'
import Trash from './trash'
import EditorModel from './createModel'
import './index.less'

@withRouter 
class Model extends Component{
  constructor(props){
    super(props);
    this.state = {
      dataSource: [],
      loading: false,
      page: 1,
      total:1,
      search:null,
      trashVisible:false,
      editorModelVisible:false,
      modelId:null,
    }
  }

  componentDidMount(){
    this.getModelList();
  }
  //获取模型列表
  getModelList(){
    const params = {
      pageNum: this.state.page,
      pageSize: 10,
      userId:2
    }
    const { page } = this.state
    this.setState({loading:true})
    getModelList(params).then(res=>{
      console.log(res)
      if(res){
        this.setState({loading:false})
        if(res.data.meta.code == '001'){
          const dataSource = res.data.data['Model List'].map((item,index)=>{
            return { ...item, index: index+1+(page-1)*10,key:item.id }
          })
          this.setState({
            dataSource,
            loading: false,
            total: res.data.data.Total,
          })
        }else{
          message.error(res.data.meta.message)
        }
      }
    }).catch(err=>{
      console.log(err)
    })
  }
  //删除模型
  handleDelect = (id) =>{
    console.log('删除模型')
    const params = {
      modelId: id
    }
    deleteModel(params).then(res=>{
      if(res){
        if(res.data.meta.code == '001'){
          message.success(res.data.meta.message)
          this.getModelList()
        }else{
          message.error(res.data.meta.message)
        }
      }
    }).catch(err=>{
      console.log(err)
    })
  }
  //获取搜索结果
  getSearchData = value => {
    let params={
      modelName : value,
      pageNum: this.state.page,
      pageSize: 10,
      userId:2
    }
    const {page}=this.state
    getSearchData(params).then(res=>{
      if(res){
        if(res.data.meta.code == '001'){
          const dataSource = res.data.data['model List'].map((item,index)=>{
            return { ...item, index: index+1+(page-1)*10,key:item.id}
          })
          this.setState({ 
            dataSource, 
            loading: false,
            total: res.data.data.Total
          })
        }else{
          message.error(res.data.meta.message)
          this.setState({ loading: false })
        }
      }
    }).catch(err=>{
      this.setState({ loading: false })
      console.log(err)
    })
  }
  //搜索模型
  handleSearch = value => {
    let that = this
    this.setState({
      page: 1
    }, function(){
      that.getSearchData(value)      
    })
  }
  //换页
  changePage = () => {
    const { search } = this.state
    search ? this.getSearchData(search) : this.getModelList()
  }

  //定位实验运行
  locateExperiments=()=>{
    console.log('定位实验运行');
  }

  //下载模型
  downloadModel=()=>{
    console.log('导出模型')
  }
  
  //编辑模型
  editorModel=(id)=>{
    this.setState({
      modelId:id,
      editorModelVisible:true
    })
  }

  render(){
    const { total, dataSource,trashVisible, editorModelVisible } = this.state
    
    const columns = [
      {title:'模型id',dataIndex:'id',key:'id'},
      {title:'模型名称', dataIndex: 'name', key: 'name'},
      {title: '实验运行id',dataIndex: 'fkRunningId', key: 'fkRunningId',
        render:(text,record)=>(
        <a onClick={()=>this.locateExperiments(text)}>{text}</a>
        )
      },
      {title: '创建时间',dataIndex:'createTime',key: 'createTime'},
      {title: '模型描述',dataIndex:'modelDesc',key: 'modelDesc'},
      {title: '操作',key: 'action',width:'150px',align:'center',
        render: (text, record) => (
          <span>
            <a onClick={()=>this.editorModel(record.id)}>编辑</a>
            <Divider type="vertical" />
            <a onClick={()=>this.handleDelect(record.id)}>删除</a>
            <Divider type="vertical" />
            <a onClick={()=>this.downloadModel(record)}>导出</a>
          </span>
        ),
      },
    ]

    return(
    <div className="modelWrapper">
      <div className="modelContent">
        <div className="listWrapper">
          <PageHeader
            style={{border: '1px solid rgb(235, 237, 240)', marginBottom: '19px'}}
            title={<span onClick={()=>this.getModelList()}>模型管理</span>}
            extra={[
                <Button type='primary' onClick={()=>{this.setState({trashVisible:true})}}>回收站</Button>,
                <Input.Search style={{width:'200px'}} onSearch={value => this.handleSearch(value)} placeholder="搜索模型" />
            ]}
          />
          <Spin spinning={this.state.loading} tip="loading...">
            <Table 
              size='small' bordered
              dataSource={dataSource} 
              columns={columns} 
              pagination={{
                onChange: page => {
                  console.log(page);
                  this.setState({page},()=>{
                    this.changePage()
                  });
                },
                current:this.state.page,
                total,
                pageSize: 10,
              }}
            />
          </Spin>
        </div>
      </div>
      
      {trashVisible&&
      <Modal
        title="回收站"
        visible={trashVisible}
        footer={null}
        onCancel={()=>{
          this.setState({trashVisible:false})
          this.getModelList()
          }}>
        <Trash/>
      </Modal>}

      {editorModelVisible&&
      <Modal
        title="编辑模型"
        visible={editorModelVisible}
        footer={null}
        onCancel={()=>{this.setState({editorModelVisible:false,modelId:null})}}>
        <EditorModel
          modelId={this.state.modelId}
          close={()=>{
            this.setState({editorModelVisible:false,modelId:null})
            this.getModelList()
          }}
        />
      </Modal>}
    </div>
    )
  }
}

export default Model;