import React, { Fragment } from 'react';
import { Card, Form, Input, Menu, Select, Spin, Tooltip, Icon } from 'antd';
import { withPropsAPI, NodePanel } from 'gg-editor';
import { connect } from 'react-redux';
import {saveParams} from '../../template/store/actionCreators'; ;

const { Item } = Form;
const { Option } = Select;

class NodeItemPanel extends React.Component {

  constructor(props){
    super(props);
    this.state = {}
  }

  componentDidMount(){
    const { componentInfo, detail } = this.props;
    const id = detail.componentId;
    if(componentInfo){
      this.setState({
        variables: componentInfo[id]
      })
    }
  }



  handleSubmit = (e) => {
    if (e && e.preventDefault) {
      e.preventDefault();
    }

    const { form, propsAPI,} = this.props;
    const { getSelected, executeCommand, update } = propsAPI;
    
    setTimeout(() => {
      form.validateFieldsAndScroll((err, values) => {
        console.log(values);
        const { nodeType, nodeRemarks, ...conf } = values
        if (err) {
          return;
        }

        const item = window.ITEM;

        if (!item) {
          return;
        }
        executeCommand(() => {
          update(item, {
            nodeType,
            nodeRemarks,
          });
        });
        var params={};
        params[item.id]=conf;
        this.props.saveParams(params);

      });
    }, 0);
  };
 
  render() {
    console.log("子组件",this.props.detail);
    console.log("组件参数", this.state.variables)
    const { 
      nodeType,
      nodeRemarks,
      conf,
      componentId
     } = this.props.detail;
    const { TextArea } = Input;
    const { form,templateParams,templateType} = this.props;
    const { variables } = this.state;
    const id=window.ITEM.id;
    return (
      <Fragment>
        <Form onSubmit={this.handleSubmit}>
          <Item label="节点名称">
            {form.getFieldDecorator('nodeName', {
              initialValue:nodeType,
              rules:[{
                validator:  (rule, val, callback)=>{
                  if(val.length > 12){
                    callback('节点名称的最大长度为12！')
                  }
                  callback();
                }
              }]
            })
            (<Input onBlur={this.handleSubmit} disabled={templateType=='0'?false:true}/>)}
          </Item>
          <Item label="备注">
            {form.getFieldDecorator('nodeRemarks', {
              initialValue:nodeRemarks,
            })
            (<TextArea onBlur={this.handleSubmit} rows={3} disabled={templateType=='0'?false:true}/>)}
          </Item>
          <Menu onClick={this.props.handleClick} selectedKeys={[this.props.configCurrentKey]} mode="horizontal">
            <Menu.Item key="configuration">
              参数配置
            </Menu.Item>
            {/* <Menu.Item key="description">
              描述说明
            </Menu.Item> */}
          </Menu>
          <Card title='算法IO参数' bordered={false} bodyStyle={{background: '#fff'}}>
            
            {
              variables && variables.map(item=>{
                return(
                  <Item label={
                    <span>
                    {item.variableName}&nbsp;
                      <Tooltip title={item.variableDes}>
                        <Icon type="question-circle-o" style={{color:'#5f6267'}} />
                      </Tooltip>
                    </span>
                    } required>
                    {form.getFieldDecorator(item.variableName, {
                      initialValue: conf[item.variableName],
                    })
                    (
                      item.variableType == 0 ? <Input onBlur={this.handleSubmit} disabled={templateType=='0'?false:true}/> : 
                        <Select onBlur={this.handleSubmit} disabled={templateType=='0'?false:true}>
                          {item.enumValues.map(arr=>
                            (<Option value={arr.value}>{arr.value}</Option>)
                          )}
                        </Select>
                    )}
                  </Item>
                )
              })
            }
          </Card>
        </Form>
      </Fragment>
    );
  }
};

const mapStateToProps = (state) => ({
  componentInfo: state.editor.componentInfo,
  templateParams:state.template.templateParams,
  templateType:state.template.templateType
})
const mapDispatchToProps=(dispatch)=>({
  saveParams(params){
    dispatch(saveParams(params))
  }
})

export default Form.create()(withPropsAPI(connect(mapStateToProps,mapDispatchToProps)(NodeItemPanel)));
// export default Form.create()(withPropsAPI(NodeItemPanel));
