import * as Types from './constants';
export const saveTemplateId=(id)=>({
    type:Types.SAVE_TEMPLATE_ID,
    templateId:id
})
export const saveTemplateType=(type)=>({
    type:Types.SAVE_TEMPLATE_TYPE,
    templateType:type
})
export const saveTemplateParams=(params)=>({
    type:Types.SAVE_TEMPLATE_PARAMS,
    params
})
export const saveParams=(params)=>({
    type:Types.SAVE_PARAMS,
    params
})
export const saveTemplateName=(name)=>({
    type:Types.SAVE_TEMPLATE_NAME,
    name
})
export const saveProcessId=(id)=>({
    type:Types.SAVE_PROCESS_ID,
    id
})
export const saveExperimentId=(id)=>({
    type:Types.SAVE_EXPERIMENT_ID,
    id
})
export const saveGGeditorObj=(obj)=>({
    type:Types.SAVE_GGEDITOR_OBJ,
    obj
})
export const saveXmlString=(xml)=>({
    type:Types.SAVE_XML_STRING,
    xml
})