import React,{ Component } from 'react'
import  { Table ,message, Divider, Badge, Modal} from 'antd'
import CreateModel from '../model/createModel'
import { getExperimentRunning, deleteExperimentRunning } from '../../api/experimets'

class ExperimentRunning extends Component{
  constructor(props){
    super(props);
    this.state={
      loading:false,
      dataSource:[],
      page:1,
      total:1,
      createModelVisible:false,       //导出模型对话框
      runningId:null
    }
  }

  componentDidMount(){
    this.getRunDataSource()
  }
  //获取实验运行记录信息
  getRunDataSource=()=>{
    this.setState({loading: true})
    const params = {
      pageNum: this.state.page,
      pageSize: 10,
      experimentId:this.props.experimentId
    }
    const {page} = this.state
    getExperimentRunning(params).then((res)=>{
        if(res){
          if(res.data.meta.code == '001'){
            const dataSource = res.data.data['ExperimentRunning List'].map((item,index)=>{
              return { ...item, index: index+1+(page-1)*10,key:item.id }
            })
            this.setState({ 
              dataSource, 
              loading: false,
              total: res.data.data.Total  
            })
          }else{
            message.error(res.data.meta.message)
          }
        }
      }).catch(err=>{
        console.log(err)
      })
  }
  //删除实验运行
  deleteRunning=(id)=>{
    const params = {
      runningId:id
    }
    console.log('删除实验'+id)
    deleteExperimentRunning(params).then(res=>{
      if(res){
        if(res.data.meta.code == '001'){
          message.success(res.data.meta.message)
          this.getRunDataSource()
        }else{
          message.error(res.data.meta.message)
        }
      }
    }).catch(err=>{
      console.log(err)
    })
  }

  render(){
    const columns = [
      {title: '运行id',dataIndex: 'id',key:'id'},
      {title: '实验id', dataIndex: 'fkExperimentId',key:'fkExperimentId'},
      {title: '开始时间', dataIndex: 'startTime',key:'startTime',width:'80px',ellipsis: true},
      {title: '结束时间', dataIndex: 'endTime',key:'endTime',width:'80px',ellipsis: true},
      {
        title: '运行状态',key: 'runningStatus',dataIndex: 'runningStatus',width:'80px',
        render: (text,record) => (
          <span>
            <Badge status={text===0 ? 'processing' : text===1 ? 'success' : 'error'} />
            {text===0 ? '运行中' : text===1 ? '成功' : '失败'}
          </span>
        ),
      },
      {title: '操作',dataIndex: 'action',width:'120px',
        render:(text,record)=>(
          <>
          <a onClick={()=>this.deleteRunning(record.id)}>删除</a>
          <Divider type='vertical'/>
          <a onClick={()=>this.setState({createModelVisible:true,runningId:record.id})}>导出模型</a>
          </>
        )},
    ];
    return(
      <div>
          <Table 
              bordered size='small'
              columns={columns} 
              dataSource={this.state.dataSource} 
              pagination={{
                onChange: page => {this.setState({page});},
                current:this.state.page,
                pageSize: this.state.pageSize,
              }}
          />

          {this.state.createModelVisible&&
          <Modal
            title="新建模型"
            visible={this.state.createModelVisible}
            footer={null}
            onCancel={()=>{this.setState({createModelVisible:false})}}
          >
            <CreateModel
              runningId={this.state.runningId}
              close={()=>{
                this.setState({createModelVisible:false})
                this.props.close()
              }}
            />
          </Modal>}
        </div>
    )
  }
}

export  default ExperimentRunning;