import React, {Component} from 'react';
import { Link, withRouter } from 'react-router-dom'
import {getBaseDataSource,selectPorcessById,savePorcess,downloadProcess,deleteProcess, searchWorkflow} from '../../api/process'
import {deleteExperiment ,stopExperiment, runExperimentRunning} from '../../api/experimets'

import { Table, Divider, Spin,Tag, PageHeader, Modal,  Input,Button,message } from 'antd';

import Trash from './trash'
import ProcessModal from'./processModal'
import ExperimentModal from'./experimentModal'
import ExperimentRunning from './experimentRunning'
import TemplateModal from './templateModal'

import './index.less'

@withRouter 
class Process extends Component{
  constructor(props){
    super(props);
    this.state = {
      dataSource: [],            //流程-数据数据列表
      runningDataSource:[],      //实验运行列表
      loading: false,            //表格加载状态
      total:1,                   //当前页的数据条数
      page: 1,                   //当前页
      pageSize:10,               //页容量
      search:'',                 //搜索结果

      newProcessVisible:false,        //新建流程对话框
      cloneProcessVisible:false,      //克隆流程对话框
      newExperimentsVisible:false,    //新建实验对话框
      cloneExperimentsVisible:false,  //克隆实验对话框
      editorExperimentsVisible:false, //修改实验对话框
      markTemplateVisible:false,      //标记实验对话框
      runningVisible:false,           //实验运行对话框
      trashVisible:false,             //回收站对话框
      
      cloneId:null ,           //克隆流程的id
      processId:null ,         //流程id    ---从指定的流程创建实验
      experimentId:null,       //实验id    ---实验运行删除，还原等操作
      selectProcessKey:'',     //定位实验 的流程id（字符串）模板新建实验功能-当实验已经存在进行的定位功能
      selectExperimentKey:null //定位实验 的实验id 把实验记录的名称标红
    }
  }
  componentDidMount(){
    this.getBaseDataSource()
  }
  //获取流程和实验信息
  getBaseDataSource=()=>{
    this.setState({loading: true})
    const params = {
      pageNum: this.state.page,
      pageSize: 10,
      isDeleted:'0',
      userId:2
    }
    const {page} = this.state
    getBaseDataSource(params).then((res)=>{
        if(res){
          if(res.data.meta.code == '001'){
            const dataSource = res.data.data.WorkflowVOList.map((item,index)=>{
              return { ...item, index: index+1+(page-1)*10,key:item.id }
            })
            this.setState({ 
              dataSource, 
              loading: false,
              total: res.data.data.Total
            })
          }else{
            message.error(res.data.meta.message)
          }
        }
      }).catch(err=>{
        console.log(err)
      })
  }
  //方法执行之后的message展示
  showMessage=(res)=>{
    if(res){
      if(res.data.meta.code == '001'){
        message.success(res.data.meta.message)
      }else{
        message.error(res.data.meta.message)
      }
    }
  }
  //保存流程
  saveProcess=(record)=>{
    console.log(record)
    const params = {
      workflowId: record.id,
      workflowXml:record.workflowXml,
      ggeditorObjectString : record.ggeditorObejectString
    }
    console.log('保存流程')
    console.log(params)
    savePorcess(params).then(res=>{
      this.showMessage(res)
    }).catch(err=>{
      console.log(err)
    })
  }

  //查看流程 ---跳转到首页，画布展示流程结构
  selectProcess=(record)=>{
    selectPorcessById(record.id).then(res=>{
      console.log(res.data)
    }).catch(err=>{
      console.log(err)
    })
  }
  
  //下载流程 
  downloadProcess=(id)=>{
    downloadProcess(id).then(res=>{
      console.log(res)
      // if(res){
      //   var blob = new Blob([res.data])
      //   console.log(blob)
      //   var href = window.URL.createObjectURL('');
      //   var link = document.createElement('a');
      //   link.setAttribute("download", 'pixture');
      //   link.href = href;
      //   link.click();
    // }
    }).catch(err=>{
      console.log(err)
    })
  }
  //搜索流程
  handleSearch = value => {
    let that = this
    this.setState({
      page: 1
    }, function(){
      that.getSearchData(value)      
    })
  }
  //换页
  changePage = () => {
    const { search } = this.state
    search ? this.getSearchData(search) : this.getBaseDataSource()
  }
  //获取搜索结果
  getSearchData=(value)=>{
    let params={
      userId:2,
      workflowName : value,
      pageNum: this.state.page,
      pageSize: 10,
    }
    const {page}=this.state
    searchWorkflow(params).then(res=>{
      if(res){
        if(res.data.meta.code == '001'){
          const dataSource = res.data.data.WorkflowVOList.map((item,index)=>{
            return { ...item, index: index+1+(page-1)*10,key:item.id}
          })
          this.setState({ 
            dataSource, 
            loading: false,
            total: res.data.data.Total
          })
        }else{
          message.error(res.data.meta.message)
          this.setState({ loading: false })
        }
      }
    }).catch(err=>{
      this.setState({ loading: false })
      console.log(err)
    })
  }
  //克隆流程 
  cloneProcess=(id)=>{
    this.setState({
      cloneProcessVisible:true,
      cloneId:id
    })
  }
  //从流程创建实验
  newExperiments=(id)=>{
    this.setState({
      newExperimentsVisible:true,
      processId:id
    })
  }
  //删除流程
  deleteProcess=(id)=>{
    console.log('删除实验'+id);
    const params={
      workflowId:id
    }
    deleteProcess(params).then(res=>{
      console.log('删除流程')
      this.showMessage(res)
      this.getBaseDataSource()
    }).catch(err=>{
      console.log(err)
    })
  }
  //编辑实验
  editorExperiment=(id)=>{
    this.setState({
      editorExperimentsVisible:true,
      experimentId:id
    })
  }
  //删除实验
  deleteExperiment=(id)=>{
    const params={
      experimentId:id
    }
    deleteExperiment(params).then(res=>{
      console.log('删除实验'+id)
      this.showMessage(res)
      this.getBaseDataSource()
    }).catch(err=>{
      console.log(err)
    })
  }
  //运行实验
  runExperiment=(id)=>{
    const params={
      experimentId:id
    }
    runExperimentRunning(params).then(res=>{
      console.log('运行实验'+id)
      this.showMessage(res)
    }).catch(err=>{
      console.log(err)
    })
  }
  //停止实验
  stopExperiment=(id)=>{
    const params={
      experimentId:id
    }
    stopExperiment(params).then(res=>{
      console.log('停止实验'+id)
      this.showMessage(res)
    }).catch(err=>{
      console.log(err)
    })
  }
  
  //克隆实验
  cloneExperiment=(id)=>{
    this.setState({
      cloneExperimentsVisible:true,
      experimentId:id
    })
  }

  render(){
    const { dataSource, newProcessVisible, cloneProcessVisible, newExperimentsVisible,    
      cloneExperimentsVisible,  editorExperimentsVisible,   markTemplateVisible,     
      runningVisible,  trashVisible,    } = this.state
   
    //流程列结构
    const columns = [
      {title: '流程id', dataIndex: 'id', key: 'id',},
      {title: '流程名称',dataIndex: 'name',key: 'name',
        render:(text,record)=>(
          <a onClick={()=>this.selectProcess(record)}>{text}</a>
        )
      },
      {title: '标签',dataIndex: 'tags', key: 'tags',
        render:tags=>(
          <>
            {tags.split(',').map((item,index)=>(
              <Tag color='orange' key={index}>{item}</Tag>
            ))}
          </>
        )},
      { title: '创建时间',dataIndex: 'createTime', key: 'createTime',},
      {title: '流程描述',dataIndex: 'workflowDesc', key: 'workflowDesc',},
      {title: '操作',key: 'action',align:'center',width:'200px',
        render: (text, record) => (
          <span>
            <a onClick={()=>{this.cloneProcess(record.id)}}>克隆流程</a>
            {/* <Divider type="vertical" />
            <a onClick={()=>{this.saveProcess(record)}}>保存</a> */}
            {/* <Divider type="vertical" />
            <a onClick={()=>{this.downloadProcess(record.id)}}>下载</a> */}
            <Divider type="vertical" />
            <a onClick={()=>{this.newExperiments(record.id)}}>创建实验</a>
            <Divider type="vertical" />
            <a onClick={()=>{this.deleteProcess(record.id)}}>删除</a>
          </span>
        )},
    ];

    //实验表结构
    const experimentsColumns = [
      { title: '实验id', dataIndex: 'id', key: 'id' },
      {title: '实验名称',dataIndex: 'name',key: 'name',
        render:(text,record)=>(
          <span style={{color:this.state.selectExperimentKey===record.id?'red':''}}>{text}</span>
        )
      },
      { title: '创建时间', dataIndex: 'createTime', key: 'createTime' },
      { title: '实验描述', dataIndex: 'experimentDesc', key: 'experimentDesc'},
      {
        title: '操作', align:'center',width:'350px',
        dataIndex: 'action',
        key: 'action',
        render: (text,record) => (
          <span>
            <a onClick={()=>this.runExperiment(record.id)}>运行</a>
            <Divider type='vertical'/>
            <a onClick={()=>this.stopExperiment(record.id)}>停止</a>
            <Divider type='vertical'/>
            <a onClick={()=>this.editorExperiment(record.id)}>编辑</a>
            <Divider type='vertical'/>
            <a onClick={()=>this.deleteExperiment(record.id)}>删除</a>
            <Divider type='vertical'/>
            <a onClick={()=>this.setState({runningVisible:true,experimentId:record.id})}>记录</a>
            <Divider type='vertical'/>
            <a onClick={()=>this.cloneExperiment(record.id)}>克隆</a>
            <Divider type='vertical'/>
            <a onClick={()=>this.setState({markTemplateVisible:true,experimentId:record.id})}>标记</a>
          </span>
        ),
      },
    ];

    return(
    <div className="processWrapper">
      <div className="processContent">
        <div className="listWrapper">
          <PageHeader
            style={{border: '1px solid rgb(235, 237, 240)', marginBottom: '19px'}}
            title={<span onClick={this.getBaseDataSource}>流程-实验管理</span>}
            extra={[
                <Input.Search style={{width:'200px'}} onSearch={this.handleSearch} placeholder="搜索流程" />,
                <Button type='primary' onClick={()=>this.setState({trashVisible:true})}>回收站</Button>,
                <Button type='primary' onClick={()=>{this.setState({newProcessVisible:true})}}>新建流程</Button>,
            ]}
          />
          <Spin spinning={this.state.loading} tip="loading...">
            <Table 
              size='small' bordered
              columns={columns}
              defaultExpandedRowKeys={this.state.selectProcessKey}
              expandedRowRender={
                record=>(record.experimentList.length !== 0
                  ? <Table columns={experimentsColumns} dataSource={record.experimentList} pagination={false} />
                  : null)}
              dataSource={dataSource}
              pagination={{
                onChange: page => {
                  console.log(page);
                  this.setState({page},()=>{
                    this.changePage()
                  });
                },
                current:this.state.page,
                pageSize: 10,
              }}
            />
          </Spin>
        </div>
      </div>
      {/* 所有modal结束后都需要刷新流程数据列表，防止其他用户操作 */}
      {newProcessVisible&&
      <Modal
        title="新建流程"
        visible={newProcessVisible}
        footer={null}
        onCancel={()=>{this.setState({newProcessVisible:false})}}
      >
        <ProcessModal close={()=>{
          this.setState({newProcessVisible:false})
          this.getBaseDataSource()
          }}/>
      </Modal>}

      {cloneProcessVisible&&
      <Modal
        title="克隆流程"
        visible={cloneProcessVisible}
        footer={null}
        onCancel={()=>{this.setState({cloneProcessVisible:false,cloneId:null})}}
      >
        <ProcessModal 
          cloneId={this.state.cloneId}
          close={()=>{
            this.setState({cloneProcessVisible:false})
            this.getBaseDataSource()
          }}
          isClone={true} 
        />
      </Modal>
      }

      {newExperimentsVisible&&
      <Modal
        title="新建实验"
        visible={newExperimentsVisible}
        footer={null}
        onCancel={()=>{this.setState({newExperimentsVisible:false})}}
      >
        <ExperimentModal
          processId={this.state.processId}
          close={()=>{
            this.setState({newExperimentsVisible:false})
            this.getBaseDataSource()
          }}
          modalType={'create'}
        />
      </Modal>}

      {editorExperimentsVisible&&
      <Modal
        title="编辑实验信息"
        visible={editorExperimentsVisible}
        footer={null}
        onCancel={()=>{this.setState({editorExperimentsVisible:false,experimentId:null})}}
      >
        <ExperimentModal
          close={()=>{
            this.setState({editorExperimentsVisible:false,experimentId:null})
            this.getBaseDataSource()
          }}
          experimentId={this.state.experimentId}
          modalType={'editor'}
        />
      </Modal>}

      {cloneExperimentsVisible&&
      <Modal
        title="克隆实验"
        visible={cloneExperimentsVisible}
        footer={null}
        onCancel={()=>{
          this.setState({cloneExperimentsVisible:false,experimentId:null})
          this.getBaseDataSource()
        }}
      >
        <ExperimentModal
          close={()=>{
            this.setState({cloneExperimentsVisible:false,experimentId:null})
            this.getBaseDataSource()
          }}
          experimentId={this.state.experimentId}
          modalType={'clone'}
        />
      </Modal>}

      {runningVisible&&
      <Modal
        title="实验运行管理" width='600px'
        visible={runningVisible}
        footer={null}
        onCancel={()=>{this.setState({runningVisible:false,experimentId:null})}}
      >
        <ExperimentRunning 
          experimentId={this.state.experimentId}
          close={()=>{
            this.setState({runningVisible:false,experimentId:null})
            this.getBaseDataSource()
          }}
        />
      </Modal>}

      {markTemplateVisible&&
      <Modal
        title="标记模板"
        visible={this.state.markTemplateVisible}
        footer={null}
        onCancel={()=>{this.setState({markTemplateVisible:false,experimentId:null})}}
      >
        <TemplateModal 
          experimentId={this.state.experimentId}
          close={()=>{
            this.setState({markTemplateVisible:false,experimentId:null})
            this.getBaseDataSource()
            }}/>
      </Modal>}

      {trashVisible&&
      <Modal
        title="回收站"
        visible={trashVisible}
        footer={null}
        onCancel={()=>{
          this.setState({trashVisible:false})
          this.getBaseDataSource()
        }}
      >
        <Trash />
      </Modal>}
    </div>
    )
  }
}
export default Process;
