import React from 'react';
import { Card } from 'antd';
import { Minimap } from 'gg-editor';

const EditorMinimap = () => {
  return (
    <Card size="small" title="缩略图" bordered={true} style={{position:'absolute', bottom:'0', height:'440px', right: '0', width: '100%'}}>
      <Minimap height={200} />
    </Card>
  );
};

export default EditorMinimap;
