import * as constants from './constants';

const defaultState = {
  rightSideVis: false,
  configCurrentKey: 'configuration',
  label: '',
  lock: false,
  currentNode: undefined,
  currentE: undefined,
  saveLabel: false
};

export default(state=defaultState, action) => {
  const newState = JSON.parse(JSON.stringify(state));
  switch(action.type) {
    case constants.SHOW_RIGHT_SIDE :
      newState.rightSideVis = true;
      newState.label = action.label;
      return newState; 
    case constants.SHOW_RIGHT_SIDE_NO_PARAM :
      newState.rightSideVis = true;
      return newState; 
    case constants.HIDE_RIGHT_SIDE :
      newState.rightSideVis = false;
      return newState; 
    case constants.CHANGE_CURRENT_KEY :
      newState.configCurrentKey = action.key;
      return newState; 
    case constants.LOCK_RIGHT_SIDE :
      newState.lock = true;
      return newState; 
    case constants.UNLOCK_RIGHT_SIDE :
      newState.lock = false;
      return newState;
    case constants.SAVE_ITEM :
      newState.currentNode = action.item; 
      return newState;
    case constants.SAVE_COMPONENT_INFO :
      return { ...newState, componentInfo:action.componentInfo }
    case constants.SAVE_DATASET_ID :
      return { ...newState, datasetId:action.datasetId }
    default:
      return state;
  }
}