import React from 'react'
import CustomMenu from "./CustomMenu";
import logo from '../../../public/bdilab.ico'
import { menus} from '../../menusConfig'


class SiderNav extends React.Component {
  render() {

    return (
      <div style={{height: '100vh'}}>
        <div style={styles.logo}>
          <img src={logo} style={{width:"32px", marginRight:"16px"}}></img> AI-Flow
        </div>
        <CustomMenu menus={menus}/>
       </div>
    )
  }
}

const styles = {
  logo: {
    height: '64px',
    padding:'0 10px 0 28px',
    overflow: 'hidden',
    color: 'rgba(0,0,0,.85)',
    fontSize: '18px',
    fontFamily: 'Avenir,-apple-system,BlinkMacSystemFont,segoe ui,Roboto,helvetica neue,Arial,noto sans,sans-serif,apple color emoji,segoe ui emoji,segoe ui symbol,noto color emoji,sans-serif',
    lineHeight: '64px',
    whiteSpace: 'nowrap',
    textDecoration: 'none'
  }
}

export default SiderNav