const mapStateToProps = state => ({
  nodesId: state.editorToolbar.nodesId,
  GGEditorObj: state.editorToolbar.GGEditorObj,
  saveXmlString: state.editorToolbar.xmlString,
  datasetId: state.editor.datasetId,
  processId: state.editorToolbar.processId,
  scatterDataList: state.editorToolbar.scatterDataList,
  heatmapDataListstate: state.editorToolbar.heatmapDataListstate
})

const mapDispatchToProps = dispatch => {
  return{
    saveNodesId(nodesId){
      dispatch(actionCreators.saveNodesId(nodesId));
    },
    saveResultList(resultList, processLogId){
      dispatch(actionCreators.saveResultList(resultList, processLogId));
    },
    saveProcessState(processState){
      dispatch(actionCreators.saveProcessState(processState));
    }
  }
}


// export default connect(mapStateToProps, mapDispatchToProps)(withPropsAPI(Run));
export default withPropsAPI(Run);
