import * as constants from './constants';

//箭头函数，({})默认返回一个对象
export const changeRerunningVisible = ()=> ({
    type: constants.CHANGE_RERUNNING_VISIBLE
})

export const changeRunningInstanceVisible = ()=> ({
    type: constants.CHANGE_RUNNING_INSTANCE_VISIBLE
})

export const changeHistoryVisible = ()=> ({
    type: constants.CHANGE_HISTORY_VISIBLE
})

export const saveProject = (obj, xmlString) => ({
    type: constants.SAVE_PROJECT,
    obj,
    xmlString
});

export const saveProcessId = processId => ({
    type: constants.SAVE_PROCESSID,
    processId
});

export const saveProcessName = processName => ({
    type: constants.SAVE_PROCESSNAME,
    processName
});

export const saveNodesId = nodesId => ({
    type: constants.SAVE_NODESID,
    nodesId
});

export const saveResultList = (resultList, processLogId) => ({
    type: constants.SAVE_RESULT_LIST,
    resultListData: resultList.slice(0,resultList.length-1),
    isModel:resultList[resultList.length-1],
    processLogId
});

export const saveProcessState = processState => ({
    type: constants.SAVE_PROCESS_STATE,
    processState
});