import * as Types from './constants';

const defaultState={
    templateId:'',
    templateType:'',
    templateParams:{},
    params:{},
    name:'',
    processId:0,
    experimentId:0,
    ggeditorObj:{},
    xmlString:''
}
export default (state=defaultState,action)=>{
    let newState=JSON.parse(JSON.stringify(state));
    switch(action.type){
        case Types.SAVE_TEMPLATE_ID:
            newState.templateId=action.templateId;
            return newState;
        case Types.SAVE_TEMPLATE_TYPE:
            newState.templateType=action.templateType;
            return newState;
        case Types.SAVE_TEMPLATE_PARAMS:
            newState.templateParams=action.params;
            return newState;
        case Types.SAVE_PARAMS:
            newState.params=action.params;
            return newState;
        case Types.SAVE_TEMPLATE_NAME:
            newState.name=action.name;
            return newState;
        case Types.SAVE_PROCESS_ID:
            newState.processId=action.id;
            return newState;
        case Types.SAVE_EXPERIMENT_ID:
            newState.experimentId=action.id;
            return newState;
        case Types.SAVE_GGEDITOR_OBJ:
            newState.ggeditorObj=action.obj;
            return newState;
        case Types.SAVE_XML_STRING:
            newState.xmlString=action.xml;
            return newState;
        default:
            return state;
    }
}