import React,{Component} from 'react'
import {Form,Input, Button,message} from 'antd'

 import { createModel,editorModel } from '../../api/model'

class CreateModal extends Component{
  constructor(props){
    super(props);
  }

  submit=()=>{
    const { getFieldValue,validateFields } = this.props.form
    validateFields(err => {
      if(!err){
        let params = {
          modelName: getFieldValue('name'),
          modelDesc: getFieldValue('desc')
          }
          console.log(params)
        //判断编辑||新建
        if(this.props.modelId){
          params={...params,modelId:this.props.modelId}
          console.log('编辑模型')
          console.log(params)
          editorModel(params).then((res)=>{
            const newRes=res.data.meta;
            if(newRes.code==='001'){
              message.success(newRes.message)
            }else{
              message.error(newRes.message)
            }
            //关闭modal
            this.props.close();
          }).catch(err=>{
            console.log(err)
          })
        }else{
          params={...params,runningId:this.props.runningId,userId:2}
          console.log('新建模型')
          console.log(params)
          createModel(params).then((res)=>{
            const newRes=res.data.meta;
            if(newRes.code==='001'){
              message.success(newRes.message)
            }else{
              message.error(newRes.message)
            }
            //关闭modal
            this.props.close();
          }).catch(err=>{
            console.log(err)
          })
        }
        
      }
    });
  }
 
  //重置
  reset=()=>{
    this.props.form.resetFields();
  }

  render(){
    const { getFieldDecorator } = this.props.form;
    return(
      <Form labelAlign='left'> 
        <Form.Item label='模型名称'>
          {getFieldDecorator('name',{rules: [{ required: true, message: 'Please input!' }]})(<Input placeholder="请输入流程名称"/>)} 
        </Form.Item>

        <Form.Item label='模型描述'>
          {getFieldDecorator('desc',{initialValue:''})(<Input.TextArea placeholder="请输入模型描述" />)} 
        </Form.Item>
        <Form.Item style={{textAlign:'center'}}>
          <Button style={{marginRight:'5px'}} type='primary'
            onClick={this.reset}
          >
            重置
          </Button>
          <Button style={{marginLeft:'5px'}} type='primary'
            onClick={this.submit}
          >
            {this.props.modelId ? '编辑':'新建'}
          </Button>
        </Form.Item>
        
      </Form>
    )
  }
}

export default Form.create()(CreateModal);