import React from "react";
import { withPropsAPI, Command } from "gg-editor";
import { connect } from 'react-redux';
import { message, Modal, Input, Form } from 'antd';
import { getSpecialNodes, getXML } from '../../components/EditorToolbar/components/getXML';
import IconFont from '../../../common/IconFont';
import {saveTemplate,insertTemplate} from '../../../api/template'
import {saveParams} from '../store/actionCreators';

import styles from '../../components/EditorToolbar/index.module.less';

class Save extends React.Component {

  constructor(props){
    super(props);
    this.state = {
      nameValue: '',
      visible: false,
      isSaveAs:false,
      obj:{},
      xmlString:''
    }
  }

  //保存画布上的流程图 
  handleClick = (p) => {
    const { propsAPI,params} = this.props;
    this.changeStateIcon();
    const obj = propsAPI.save();
    console.log('obj',obj);
    // const { nodes, edges } = obj;
    // let specialNodes;
    // let xmlString;
    
    // if(nodes) {
    // //有边才会有网关节点
    // if(edges) {
    //     specialNodes = getSpecialNodes(obj,params);
    //     this.props.saveParams(params);
    //     console.log(specialNodes)
    //     if(specialNodes.firstNode.length !== 1 || specialNodes.firstNode[0].label !== 'start'){
    //         message.error("请加入源点或图形错误！")
    //     }else{
    //         xmlString = getXML(nodes,edges,specialNodes);
    //     }
    //     console.log('xml',xmlString);
        this.setState({
            isSaveAs:p,
        },()=>{
            if(p){
                this.setState({
                    visible:true
                })
            }else{
                this.handleSaveXML();
            }
        })
        
    // }else{
    //     message.info('没有任务流程！')
    // }
    // }
    // else {
    //     message.info('没有任务节点！')
    // }
};

  changeStateIcon = () => {
    const { propsAPI } = this.props;
    const { executeCommand, update, getSelected, find } = propsAPI;
    // const nodesId = {};
    const selectedNodes = getSelected();
    console.log(selectedNodes);
    for(let i=0;i<selectedNodes.length;i++){
      const item = selectedNodes[i];
    //   const label = item.model.label;
    //   const id = item.id;
    //   nodesId[label] = id;
      executeCommand(() => {
        console.log(item)
        update(item, {
          state_icon_url:'',
        });
      });
    }
  }

//   保存XML字符串和GGEditor对象
  handleSaveXML = (v) => {
      const {templateId,params,ggeditorObj,xmlString}=this.props;
      const {isSaveAs}=this.state;
      var param;
      if(isSaveAs){
        param = { 
          ...v,
          paramsJsonString:JSON.stringify(params),
          userId:2,
          originTemplateId:templateId
        }
        insertTemplate(param).then((res)=>{
          if(res){
            if(res.data.meta.code === '001'){
              
              message.success('保存成功！')
            }else{
              message.error('保存失败！'); 
            }
          }else{
            message.error('保存失败！'); 
          }
        }).catch(error=>{
          console.log(error)
        });
        
      }else{
        param = {
          templateId,
          paramsJsonString:JSON.stringify(params)
        }
        saveTemplate(param).then((res)=>{
          if(res){
            if(res.data.meta.code === '001'){
              
              message.success('保存成功！')
            }else{
              message.error('保存失败！'); 
            }
          }else{
            message.error('保存失败！'); 
          }
        }).catch(error=>{
          console.log(error)
        });
    };
  }
  handleCancel=()=>{
      this.setState({
          visible:false
      })
      this.props.form.resetFields();
  }
  handleOk=()=>{
    //   const {obj, xmlString,isSaveAs}=this.state;
      this.props.form.validateFields((err,values)=>{
        if(!err){
            this.handleSaveXML(values);
        }
      })
      this.props.form.resetFields();
  }
  render() {
    const {title}=this.props;
    const {visible}=this.state;
    const rules=[{required:true,whitespace: true,}];
    const {getFieldDecorator}=this.props.form;
    return (
      <div>
        <Command name="selectAll">
            {title?
            (
                <div className={styles.com} title='保存' onClick={()=>this.handleClick(false)}>
                    <IconFont type={`icon-baocun`}/><span>保存</span>
                </div>
            ):
            (
                <div className={styles.com} title='另存' onClick={()=>this.handleClick(true)}>
                    <IconFont type={`icon-baocun`}/><span>另存</span>
                </div>
            )  
            }
        </Command>
        <Modal
            title='另存模板'
            visible={visible}
            onCancel={this.handleCancel}
            onOk={this.handleOk}
        >
            <Form labelCol={{span:6}} wrapperCol={{span:12}}>
                <Form.Item label='模板名称' >
                    {
                        getFieldDecorator('templateName',{
                            rules,
                        })(<Input placeholder='请输入模板名称'/>)
                    }
                </Form.Item>
                <Form.Item label='模板标签'>
                    {
                        getFieldDecorator('tags',{
                            rules,
                        })(<Input placeholder='请输入模板标签以‘，’隔开'/>)
                    }
                </Form.Item>
                <Form.Item label='模板描述' >
                    {
                        getFieldDecorator('templateDesc',{
                            rules,
                        })(<Input placeholder='请输入模板描述'/>)
                    }
                </Form.Item>
            </Form>
        </Modal>
      </div>
    );
  }
}

const mapStateToProps=(state)=>({
    templateId:state.template.templateId,
    templateType:state.template.templateType,
    params:state.template.params,
    ggeditorObj:state.template.ggeditorObj,
    xmlString:state.template.xmlString
})

const mapDispatchToProps=(dispatch)=>({
  saveParams(params){
    dispatch(saveParams(params))
  }
})
export default connect(mapStateToProps, mapDispatchToProps)(Form.create()(withPropsAPI(Save)));
// export default withPropsAPI(Save);