const mapStateToProps = (state) => {
  console.log(state)
  return({
  rerunningVisible: state.editorToolbar.rerunningVisible,
  runningInstanceVisible: state.editorToolbar.runningInstanceVisible,
  historyVisible: state.editorToolbar.historyVisible,
  processName: state.editorToolbar.processName,
  rerunningInstanceNum: 0,
  parameterInstanceNum: 0,
  orderInstanceNum: 0,
})}

const mapDispatchToProps = (dispatch) => ({
  changeRerunningVisible() {
    dispatch(actionCreators.changeRerunningVisible());
  }, 
  changeRunningInstanceVisible() {
    dispatch(actionCreators.changeRunningInstanceVisible());
  },
  changeHistoryVisible() {
    dispatch(actionCreators.changeHistoryVisible());
  }
})

// export default connect(mapStateToProps,mapDispatchToProps)(FlowToolbar);
export default FlowToolbar;
