import React,{Component} from 'react';
import {PageHeader,Button,Input,Spin,Table,Modal,List,Tabs,Form,Divider,Popover,Select,Tag,message,Popconfirm} from 'antd';
import { Row, Col} from 'antd';
import GGEditor, { Flow ,withPropsAPI} from 'gg-editor';
import { FlowContextMenu } from '../components/EditorContextMenu';
import FlowToolbar  from './components/flowToolbar';
import { FlowDetailPanel } from '../components/EditorDetailPanel';
import CustomNode from '../components/node/nodeTensorflow';
import StartNode from '../components/node/nodeStart';
import * as actionCreators from './store/actionCreators';
import {connect} from 'react-redux';
import IconFont from '../../common/IconFont';
import CreateExperiment from './components/createExperiment';
import {getTemplate,deleteTemplate,searchTemplate,restoreTemplate,createExperiment} from '../../api/template';
import {withRouter} from 'react-router-dom'
import '../components/EditorDetailPanel/index.less';
import styles from '../home/index.module.less';
import './index.less';

@withRouter
class TemplateManage extends Component{

    state={
        templateType:0,
        dataSource:[],
        dataInTrash:[],
        deleteVisible:false,
        listLoading:false,
        modalLoading:false,
        deleteItem:[],
        editorVisible:false,
        visible:{},
        selectValue:'name',
        page:1,
        total:1,
        trashPage:1,
        trashTotal:1,
        keyword:'',
        createVisible:false,
        processName:'1',
        processId:'',
        ggeditorObj:{},
        isRestore:false,
        editorTemplate:{}
    }
    getTemplate=()=>{
        const {templateType,page}=this.state;
        this.setState({
            listLoading:true
        })
        const params={
            type:templateType,
            pageNum:page,
            pageSize:5,
            isDeleted:0,
            userId:2
        }
        getTemplate(params).then(res=>{
            if(res.data.meta.code==='001'){
                const data=res.data.data;
                console.log(data)
                const visible={};
                const dataSource=data.templateVOList.map((item,index)=>({
                    ...item,
                    index:index+1+(page-1)*10
                }))
                for(let i of data.templateVOList){
                    visible[i.id]=false
                }
                console.log(visible);
                this.setState({
                    listLoading:false,
                    dataSource,
                    visible,
                    total:data.Total
                })
            }else{
                message.error('获取数据失败!');
                this.setState({
                    listLoading:false,
                }) 
            }
        }).catch(err=>{
            message.error('获取数据失败!');
            this.setState({
                listLoading:false,
            }) 
        })
    }
    getTemplateInTrash=()=>{
        const {trashPage}=this.state;
        this.setState({
            modalLoading:true
        })
        const params={
            pageNum:trashPage,
            pageSize:6,
            type:1,
            isDeleted:1,
            userId:2
        }
        getTemplate(params).then(res=>{
            if(res.data.meta.code==='001'){
                const data=res.data.data;
                const dataInTrash=data.templateVOList.map((item,index)=>({
                    ...item,
                    index:index+1+(trashPage-1)*10,
                    key:item.id
                }))
                this.setState({
                    modalLoading:false,
                    dataInTrash,
                    trashTotal:data.Total
                })
            }else{
                message.error('获取数据失败!');
                this.setState({
                    modalLoading:false,
                }) 
            }
        }).catch(err=>{
            message.error('获取数据失败!');
            this.setState({
                modalLoading:false,
            }) 
        })
    }
    changeKeyword=(e)=>{
        this.setState({
            keyword:e.target.value
        })
    }
    handleSearch=()=>{
        var {keyword}=this.state;
        keyword.replace(/\s/g,(a)=>'');
        if(keyword==''){
            message.warning('搜索关键字不能为空格字符串！')
        }else{
            this.setState({
                page:1
            },this.searchTemplate)
        }
    }
    searchTemplate=()=>{
        const {page,templateType,keyword,selectValue}=this.state;
        this.setState({
            listLoading:true
        })
        const params={
            type:templateType,
            pageNum:page,
            pageSize:5,
            userId:2
        }
        if(selectValue=='name'){
            params.name=keyword;
        }else{
            params.tag=keyword
        }
        searchTemplate(params).then(res=>{
            if(res.data.meta.code==='001'){
                const data=res.data.data;
                const visible={};
                const dataSource=data.templateVOList.map((item,index)=>({
                    ...item,
                    index:index+1+(page-1)*10
                }))
                for(let i of data.templateVOList){
                    visible[i.id]=false
                }
                this.setState({
                    listLoading:false,
                    dataSource,
                    visible,
                    total:data.Total
                })
            }else{
                message.error('搜索失败!');
                this.setState({
                    listLoading:false,
                }) 
            }
        }).catch(err=>{
            message.error('搜索失败!');
            this.setState({
                listLoading:false,
            }) 
        })
    }
    changeType=(key)=>{
        console.log(key)
        this.setState({
            templateType:parseInt(key),
            page:1
        },()=>{
            this.getTemplate();
        })
    }
    openModal=()=>{
        this.setState({
            deleteVisible:true,
            trashPage:1
        },this.getTemplateInTrash)
    }
    closeModal=()=>{
        this.setState({
            deleteVisible:false
        })
        this.getTemplate();
    }
    changeDeleteItem=(selectedRowKeys,selectedRows)=>{
        this.setState({
            deleteItem:selectedRowKeys
        })
    }
    createExperiment=()=>{
        const {editorTemplate}=this.state
        this.createForm.props.form.validateFields((err,values)=>{
            if(!err){
                console.log(values);
                const params={
                    ...values,
                    templateId:editorTemplate.id,
                    userId:2
                }
                // if(this.state.processId){
                //     params.processId=this.state.processId;
                // }
                createExperiment(params).then(res=>{
                    if(res.data.meta.code=='001'){
                        console.log(res.data)
                        message.success('创建实验成功!');
                    }else{
                        message.success('创建实验失败!');
                    }
                }).catch(err=>{
                    message.success('创建实验成功!');
                })
                this.setState({
                    createVisible:false
                })
                this.createForm.props.form.resetFields();
            }
        })
    }
    closecreateExperiment=()=>{
        this.setState({
            createVisible:false
        })
        this.createForm.props.form.resetFields();
    }
    editorTemplate=(item)=>{
        this.props.saveTemplateId(item.id);
        this.props.saveTemplateType(item.type);
        this.props.saveTemplateParams(item.paramJsonString);
        this.props.saveTemplateName(item.name);
        this.props.saveObjXml(item.ggeditorObjectString,item.workflowXml);
        this.setState({
            ggeditorObj:item.ggeditorObjectString,
            templateParams:item.paramJsonString
        })
    }
    viewTemplateInfo=(item)=>{
        this.setState({
            ggeditorObj:item.ggeditorObjectString,
            templateParams:item.paramJsonString
        })
        this.props.saveTemplateName(item.name);
        this.props.saveTemplateParams(item.paramJsonString);
        this.props.saveTemplateType(item.type);
    }
    clickRight=(id)=>{
        const {visible}=this.state;
        for(let i in visible){
            visible[i]=false;
        }
        visible[id]=true;
        console.log(visible)
        this.setState({
            visible
        })
        window.event.preventDefault();
    }
    renderList=(dataSource,templateType)=>{
        const style={cursor:'pointer'}
        const {page,total,keyword}=this.state;
        return (
            <List
                dataSource={dataSource}
                itemLayout='horizontal'
                size={templateType==='public'?'default':'small'}
                pagination={{
                    pageSize:5,
                    size:'small',
                    current:page,
                    total,
                    onChange:(page)=>{
                        this.setState({
                            page
                        },()=>{
                            if(keyword){
                                this.searchTemplate();
                            }else{
                                this.getTemplate();
                            }
                        })
                    }
                }}
                renderItem={(item)=>(
                    // <Popover
                    //     content={<div>
                    //         <p style={style} onClick={()=>this.openCreateModal(item)}>创建实验</p>
                    //         {templateType===0?null:<p style={style} onClick={()=>this.deleteTemplate(item.id)}>删除</p>}
                    //         {templateType===0?<p style={style} onClick={()=>this.viewTemplateInfo(item)}>查看模板信息</p>:<p onClick={()=>this.editorTemplate(item)} style={style}>编辑</p>}
                    //     </div>}
                    //     trigger='click'
                    //     visible={this.state.visible[item.id]}
                    //     placement='rightTop'
                    // >
                        <List.Item
                            key={item.id}
                            onContextMenu={()=>this.clickRight(item.id)}
                            style={{padding:'4px 0'}}
                            actions={[
                                <a style={{fontSize:12}} onClick={()=>this.openCreateModal(item)}>创建实验</a>,
                                templateType===0?<a style={{fontSize:12}} onClick={()=>this.viewTemplateInfo(item)}>查看模板信息</a>:<a onClick={()=>this.editorTemplate(item)} style={{fontSize:12}}>编辑</a>,
                                templateType===0?null:<a style={{fontSize:12}} onClick={()=>this.deleteTemplate(item.id)}>删除</a>,
                            ]}
                        >
                            <List.Item.Meta
                                title={<div>
                                    <span>{item.name}</span>
                                    <div>
                                        {item.tags.split(',').map(i=>(
                                            <Tag color="orange" style={{height:20,padding:'0 5px',marginRight:5,fontSize:12}}>{i}</Tag>
                                        ))}
                                    </div>
                                </div>}
                                // description={templateType==='public'?null:item.desc}
                                description={item.templateDesc}
                            />
                        </List.Item>
                    // {/* </Popover> */}
                )}
            />
        )
    }
    openCreateModal=(item)=>{
        // const params={
        //     id
        // }
        // getProcessInfo(params).then(res=>{
        //     if(res.data.meta.code==='001'){
        //         this.setState({

        //         })
        //     }
        // }).catch(err=>{

        // })
        if(item.type!==0){
            if(item.fkExperimentId){
                console.log(item.isDeleted,item.id)
                if(item.experimentIsDeleted){
                    this.setState({
                        isRestore:true
                    })
                }else{
                    this.props.saveProcessId(item.fkWorkflowId);
                    this.props.saveExperimentId(item.fkExperimentId);
                    this.props.history.push('/process')
                }
            }else{
                this.setState({
                    createVisible:true,
                    editorTemplate:item
                })
            }
        }else{
            this.setState({
                createVisible:true,
                editorTemplate:item
            })
        }
        
    }
    restoreExperiment=()=>{

    }
    notRestore=()=>{
        this.setState({
            isRestore:false
        })
    }
    deleteTemplate=(id)=>{
        const params={
            templateIds:id+''
        };
        deleteTemplate(params).then(res=>{
            if(res.data.meta.code==='001'){
                message.success('删除成功!');
            }else{
                message.error('删除失败!');
            }
            this.getTemplate();
        }).catch(err=>{
            message.error('删除失败!');
        })
    }
    changeSelectValue=(v)=>{
        this.setState({
            selectValue:v
        })
    }
    deleteTemplatePermanently=(id)=>{
        let params={};
        const {deleteItem}=this.state;
        if(typeof id=='number'){
            params.templateIds=id+'';
        }else{
            params.templateIds=deleteItem.join(',');
        }
        deleteTemplate(params).then(res=>{
            if(res.data.meta.code==='001'){
                message.success('删除成功!');
            }else{
                message.error('删除失败!');
            }
            this.getTemplateInTrash();
        }).catch(err=>{
            message.error('删除失败!');
        })
    }
    restoreTemplate=(id)=>{
        console.log(id)
        let params={};
        const {deleteItem}=this.state;
        if(typeof id =='number'){
            params.templateIds=id+'';
        }else{
            params.templateIds=deleteItem.join(',');
        }
        restoreTemplate(params).then(res=>{
            if(res.data.meta.code==='001'){
                message.success('还原成功!');
            }else{
                message.error('还原失败!');
            }
            this.getTemplateInTrash();
        }).catch(err=>{
            message.error('还原失败!');
        })
    }

    componentDidMount=()=>{
        this.getTemplate();
        window.onclick=()=>{
            const {visible}=this.state;
            for(let i in visible){
                visible[i]=false;
            }
            this.setState({
                visible
            })
        }
    }
    lockIconVis = () => {
        const { unlockDrawer, lockDrawer, lock, rightSideVis } = this.props;
        return (
          <div className={styles.lockIcon}>
            {/* <IconFont type="icon-suo" className={ lock ? styles.lock : styles.unlock } onClick={ lock ? unlockDrawer: lockDrawer}/> */}
            <IconFont type="icon-suo" className={styles.lock} />
          </div>
        )
      }
    componentWillUnmount(){
        this.props.saveTemplateName('');
        this.props.saveTemplateParams({});
        // this.props.saveProcessId(-1);
        // this.props.saveExperimentId(-1);
    }
    
    render(){
        const {templateType,editorTemplate,isRestore,deleteVisible,listLoading,modalLoading,dataSource,dataInTrash,deleteItem,ggeditorObj,selectValue,trashPage,trashTotal,keyword,createVisible}=this.state;
        const { handleNodeClick, handleCanvasClick, rightSideVis, lock, currentNode } = this.props;
        const columns1=[
            {
                title:'模板名',
                key:'name',
                dataIndex:'name',
                align:'center'
            },
            {
                title:'模板标签',
                key:'tags',
                dataIndex:'tags',
                align:'center',
                render:(text,record)=>{
                    return text.split(',').map(i=>(
                        <Tag color="orange" style={{height:20,padding:'0 5px',marginRight:5,fontSize:12}}>{i}</Tag>
                    ))
                }
            },
            {
                title:'模板描述',
                key:'templateDesc',
                dataIndex:'templateDesc',
                align:'center'
            },
            {
                title:'操作',
                key:'action',
                dataIndex:'action',
                render:(text,record)=>{
                    return (
                        <div>
                            <a onClick={()=>this.deleteTemplatePermanently(record.id)}>永久删除</a>
                            <Divider type='vertical' />
                            <a onClick={()=>this.restoreTemplate(record.id)}>还原</a>
                        </div>
                    )
                },
                align:'center'
            }
        ];
        const col={
            labelCol:{span:6},
            wrapperCol:{span:12}
        }
        return (
            <div className='templateWrap'>
                <PageHeader
                        style={{border:'1px solid rgb(235, 237, 240)',marginRight:10,padding:'10px 24px'}}
                        title='模板管理'
                        subTitle='已有模板列表'
                        extra={[
                            <Select value={selectValue} onChange={this.changeSelectValue}>
                                <Select.Option value='name'>按名称搜索</Select.Option>
                                <Select.Option value='tag'>按标签搜索</Select.Option>
                            </Select>,
                            <Input.Search placeholder='搜索模板' onSearch={this.handleSearch} value={keyword} onChange={this.changeKeyword} style={{width:200,margin:'0 10px'}}  />,
                            <Button type="primary" onClick={this.openModal}>回收站</Button>
                        ]}
                />   
                <div className='templateContent'>
                    <div className='listWrap'>   
                        <Spin spinning={listLoading}>
                            <Tabs onChange={this.changeType} defaultActiveKey="0" tabBarGutter={10} >
                                <Tabs.TabPane tab="提供的模板" key="0" >
                                    {listLoading?this.renderList([],templateType):this.renderList(dataSource,templateType)}
                                </Tabs.TabPane>
                                <Tabs.TabPane tab="自定义模板" key="1">
                                    {listLoading?this.renderList([],templateType):this.renderList(dataSource,templateType)}
                                </Tabs.TabPane>
                            </Tabs>
                        </Spin>
                    </div>
                    <div className='ggeditorWrap'>
                        <GGEditor className={styles.editor}>
                            <Row type="flex" className={styles.editorBd} style={{height:'100%'}}>
                                {/* <Col xxl={5} xl={6} lg={7} md={8} sm={9} xs={9} className={styles.editorSidebar}>
                                    <FlowItemPanel custom='no'/>
                                </Col> */}
                                <Col xxl={18} xl={17} lg={16} md={15} sm={14} xs={14} className={styles.editorContent}>
                                    <FlowToolbar/>
                                    <CustomNode />
                                    <StartNode />
                                    <Flow 
                                    className={styles.flow}
                                    // onNodeClick={(e)=>{handleNodeClick(e);}}
                                    // onClick={(e)=>{handleCanvasClick(rightSideVis, lock, currentNode);}}
                                    data={ggeditorObj}
                                    />
                                </Col>
                                <Col xxl={6} xl={7} lg={8} md={9} sm={10} xs={10} className={styles.editorSidebar}>
                                    <FlowDetailPanel/>
                                    {/* <EditorMinimap/>  */}
                                </Col>
                            </Row>
                            <FlowContextMenu />
                            {/* <Drawer
                                placement="right"
                                mask={false}
                                style={{marginTop:'64px'}}
                                width='400px'
                                closable={false}
                                visible={ rightSideVis || lock }
                            >
                                {this.lockIconVis()}
                                <FlowDetailPanel/> */}
                                {/* <EditorMinimap/>  */}
                            {/* </Drawer> */}
                        </GGEditor>
                    </div>
                </div>
                <Modal
                    title='回收站'
                    footer={null}
                    visible={deleteVisible}
                    onCancel={this.closeModal}
                    width={800}
                >
                    <div>
                        <Spin spinning={modalLoading}>
                            <Table
                                columns={columns1}
                                dataSource={dataInTrash}
                                rowSelection={{
                                    type:'checkbox',
                                    onChange:this.changeDeleteItem
                                }}
                                pagination={{
                                    pageSize: 6,
                                    total:trashTotal,
                                    current:trashPage,
                                    onChange:(page)=>{
                                        this.setState({
                                            trashPage:page
                                        },this.getTemplateInTrash)
                                    }
                                  }}
                            />
                        </Spin>
                        <Button type='primary' style={{marginRight:10}} onClick={this.deleteTemplatePermanently}>批量删除</Button>
                        <Button type='primary' onClick={()=>this.restoreTemplate(deleteItem)}>批量还原</Button>
                    </div>
                </Modal>
                <Modal
                    title='创建实验'
                    visible={createVisible}
                    onOk={this.createExperiment}
                    onCancel={this.closecreateExperiment}
                    okText='确定'
                    cancelText='取消'
                >
                    <CreateExperiment onRef={ref=>this.createForm=ref} processId={editorTemplate.fkWorkflowId}/>
                </Modal>
                <Modal
                    visible={isRestore}
                    onOk={this.restoreExperiment}
                    onCancel={this.notRestore}
                    okText='还原'
                    cancelText='取消'
                >
                    标记为模板的实验已被放入回收站，是否需要还原该实验?
                </Modal>
            </div>
        )
    }
}
const mapStateToProps=(state)=>({

})
const mapDispatchToProps=(dispatch)=>({
    saveTemplateId(id){
        dispatch(actionCreators.saveTemplateId(id));
    },
    saveTemplateType(type){
        dispatch(actionCreators.saveTemplateType(type));
    },
    saveTemplateParams(param){
        dispatch(actionCreators.saveTemplateParams(param));
    },
    saveTemplateName(name){
        dispatch(actionCreators.saveTemplateName(name))
    },
    saveProcessId(id){
        dispatch(actionCreators.saveProcessId(id))
    },
    saveExperimentId(id){
        dispatch(actionCreators.saveExperimentId(id))
    },
    saveObjXml(obj,xml){
        dispatch(actionCreators.saveGGeditorObj(obj));
        dispatch(actionCreators.saveXmlString(xml));
    }
})
// export default TemplateManage;
export default connect(mapStateToProps, mapDispatchToProps)(withPropsAPI(TemplateManage));
