import * as constants from './constants';

export const showRightSide = (label) => ({
  type: constants.SHOW_RIGHT_SIDE,
  label
});
export const showRightSideNoParam = () => ({
  type: constants.SHOW_RIGHT_SIDE_NO_PARAM
});
export const hideRightSide = () => ({
  type: constants.HIDE_RIGHT_SIDE
});
export const changeConfCurrent = (key) => ({
  type: constants.CHANGE_CURRENT_KEY,
  key
});
export const lockRightSide = () => ({
  type: constants.LOCK_RIGHT_SIDE,
});
export const unlockRightSide = () => ({
  type: constants.UNLOCK_RIGHT_SIDE,
});
export const saveItemObj = (item) => ({
  type: constants.SAVE_ITEM,
  item
});
export const saveComponentInfo = componentInfo => ({
  type: constants.SAVE_COMPONENT_INFO,
  componentInfo
});
export const saveDatasetId = datasetId => ({
  type: constants.SAVE_DATASET_ID,
  datasetId
});