import React from 'react'
import {  Switch, Redirect, Route } from 'react-router-dom'


import { isAuthenticated } from './utils/Session'

import Home from './pages/home'
import Dataset from './pages/dataset'
import Process from './pages/process'
import Model from './pages/model'

import CustomComponent from './pages/customcomponent'
import templateManage from './pages/template'

// import Login from './pages/login'

const PrivateRoute = ({component: Component, ...rest}) => (
    // <Route {...rest} render={(props) => (  // 判断是否登录
    //   !!isAuthenticated()
    //     ? <Component {...props} />
    //     : <Redirect to={{
    //       pathname: '/login',
    //       state: {from: props.location}
    //     }}/>
    // )}/>
    <Route {...rest} render={(props) => (  // 判断是否登录
         <Component {...props} />
    )}/>
)

const Routes =
    <Switch>
        {/* <Route exact path='/login' component={Login}></Route> */}
        <PrivateRoute exact path='/home' component={Home}/>
        <PrivateRoute exact path='/dataset' component={Dataset}/>
        <PrivateRoute exact path='/process' component={Process}/>
        <PrivateRoute exact path='/model' component={Model}/>
        <PrivateRoute exact path='/component' component={CustomComponent}/>
        <PrivateRoute exact path='/template' component={templateManage}/>
        
        <Redirect exact from='/' to='/home'/>
    </Switch>


export {Routes};