import React, {Component}from 'react';
import { connect } from 'react-redux';
import actionCreators  from '../store/actionCreators';
import { Divider, Menu, Dropdown, Icon, Modal, Button, DatePicker } from 'antd';
import moment from 'moment';
import { Toolbar, Command } from 'gg-editor';
import IconFont from '../../../common/IconFont';
import Save from './save';
import styles from '../../components/EditorToolbar/index.module.less';

class FlowToolbar extends Component {

  render() {
    return(
      <div className={styles.canvaHeader}>
        <div className={styles.canvaTitle}>
          <span className={styles.taskflow}>
              <span>
                { this.props.templateName || '请选择模板' } &nbsp;&nbsp;&nbsp;
              </span>
          </span>
        </div>
        <Toolbar className={styles.toolbar}>
          <Save />
          <Save title='另存'/>
          <Divider type="vertical" className={styles.divider} />
          <Command name="copy">
            <div className={styles.com} title="复制" >
              <IconFont type={`icon-copy`}/><span>复制</span>
            </div>
          </Command>
          <Command name="paste">
            <div className={styles.com} title="粘贴" >
              <IconFont type={`icon-paste`}/><span>粘贴</span>
            </div>
          </Command>
          <Command name="delete">
            <div className={styles.com} title="删除" >
              <IconFont type={`icon-delete`}/><span>删除</span>
            </div>
          </Command>
          <Divider type="vertical" className={styles.divider} />
          <Command name="zoomIn">
            <div className={styles.com} title="放大" >
              <IconFont type={`icon-zoom-in`}/><span>放大</span>
            </div>
          </Command>
          <Command name="zoomOut">
            <div className={styles.com} title="缩小" >
              <IconFont type={`icon-zoom-out`}/><span>缩小</span>
            </div>
          </Command>
        </Toolbar>
        <span className={styles.runningMode} title="并行运行方式">
            <IconFont type={`icon-repeat`}/>
        </span>
      </div>
        )
    }
};
const mapStateToProps=(state)=>({
  templateName:state.template.name
});
const mapDispatchToProps=(dispatch)=>({

})
export default connect(mapStateToProps,mapDispatchToProps)(FlowToolbar);
