  //     //       }
  //     //       message.success('保存成功！')
  //     //     }else{
  //     //       message.error('保存失败！'); 
  //     //     }
  //     //   }else{
  //     //     message.error('保存失败！'); 
  //     //   }
  //       // this.setState({
  //       //   visible: false,
  //       //   loading: false,
  //       //   nameValue: ''
  //       // });
  //     }).catch(error=>{
  //       console.log(error)
